body {
    background: no-repeat url('https://i.pinimg.com/originals/aa/46/be/aa46beaee67d41a7e5fb027b1ffca0f1.png');
    background-size: cover;
    color: white;
    font-family: 'Inter', sans-serif;
}

.title {
    margin: auto;
}

.card--container > img {
    height: 218px;
    width: 190px;
}

.cards--container {
    display: flex;
    text-align: center;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 5vh;
}

.card--container {
    background: #3361FF;
    margin-left: 10px;
    padding: 5px;
    border-radius: 5px;
    width: 400px;
    margin-top: 10px;
}

.score--container {
    display: flex;
    flex-direction: column;
    margin-top: 3vh;
}